.about-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  padding: 1rem 0rem 1rem 6rem;
}
.about-container .about-info {
  flex: 1 1 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about-container .about-info .about-info-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.about-container .about-info .about-info-details h1 {
  font-size: 6.2rem;
  font-weight: 600;
  margin: 0;
  line-height: 1;
}
.about-container .about-info .about-info-details h2 {
  font-size: 2.5rem;
  font-weight: 400;
  color: #7127BA;
  margin-bottom: 2rem;
}
.about-container .about-info .about-info-details h3 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}
.about-container .about-info .about-info-details p {
  font-size: 1.4rem;
  font-weight: 300;
  color: #E6E6E6;
  line-height: 1.6;
}
.about-container .about-info .about-contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}
.about-container .about-info .about-contact h1 {
  font-weight: 600;
  font-size: 1.8rem;
}
.about-container .about-info .about-contact .about-contact-icons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  gap: 3.5rem;
}
.about-container .about-picture {
  flex: 1 1 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-container .about-picture img {
  width: auto;
  height: 980px;
}